// Series navigation styling
.series-nav {
 margin: 2em 0;
 padding: 1.5em;
 width: 100%;
 background-color: var(--global-bg-color);
 border: 2px solid var(--global-theme-color);
 border-radius: 4px;
 transition: all 0.2s ease;

 .series-header {
 margin-bottom: 1.5em;
 border: none !important;
 border-bottom: none !important;
 padding: 0;

 .title-row {
 display: flex;
 align-items: center;
 gap: 1em;
 margin-bottom: 0.5em;
 }

 h3 {
 margin: 0;
 padding: 0;
 border: none !important;
 border-bottom: none !important;
 font-size: 1.5em;
 font-weight: 600;
 &::after {
 display: none !important;
 }
 }

 p {
 margin: 0;
 padding: 0;
 color: var(--global-text-color);
 opacity: 0.8;
 font-size: 0.95em;
 }
 }

 .part-badge {
 background-color: var(--global-theme-color);
 color: var(--global-bg-color);
 padding: 0.3em 0.6em;
 border-radius: 3px;
 font-size: 0.9em;
 font-weight: 500;
 white-space: nowrap;
 }

 .posts {
 display: flex;
 flex-direction: column;
 gap: 0.4em;

 a {
 text-decoration: none;
 border-bottom: none;
 
 &:hover {
 text-decoration: none;
 border-bottom: none;
 }
 }
 }

 .post {
 display: flex;
 align-items: flex-start;
 transition: all 0.2s ease;
 padding: 0.35em 0.5em;
 border-radius: 4px;
 border: 2px solid var(--global-theme-color);
 
 &.inactive {
 cursor: pointer;
 text-decoration: none;
 color: var(--global-text-color);
 border-bottom: 2px solid var(--global-theme-color);
 opacity: 0.4;
 transition: all 0.2s ease;

 * {
 transition: all 0.2s ease;
 }

 &:hover {
 border: 2px solid var(--global-theme-color);
 text-decoration: none;
 opacity: 1;
 // Light theme hover
 html[data-theme='light'] & {
 background-color: rgba(0, 0, 0, 0.05);
 }
 
 // Dark theme hover
 html[data-theme='dark'] & {
 background-color: rgba(255, 255, 255, 0.05);
 }
 
 .post-description {
 opacity: 1;
 }
 }
 }
 }

 .post-content {
 flex: 1;
 min-width: 0;
 }

 .post-title {
 font-weight: 600;
 text-decoration: none;
 color: var(--global-text-color);
 font-size: 1em;
 transition: all 0.2s ease;
 line-height: 1.2;
 }

 .post-description {
 display: -webkit-box;
 -webkit-line-clamp: 2;
 -webkit-box-orient: vertical;
 overflow: hidden;
 margin-top: 0.1em;
 color: var(--global-text-color);
 opacity: 0.8;
 font-style: italic;
 font-size: 0.85em;
 line-height: 1.2;
 transition: all 0.2s ease;
 }
} 